export default {
	data() {
		return {
			source: 0,
			addressList: [],

			// 前台用户标志
			// result: [],
			delBtnWidth: 60, //删除按钮宽度单位（rpx）
			startX: '',
		}
	},
	onLoad(option) {
		this.queryAddress();
		if (option != undefined) {
			this.source = option.source;
		}
	},
	methods: {
		//查询地址
		queryAddress() {
			this.$minApi.uniapp_post(this.$base_api.queryAddress, {
				mallId: this.$base_api.MALL_ID
			}).then(res => {
				if (res.success) {
					console.log("查询地址结果", res.data)
					this.addressList = res.data;
					this.addressList.forEach(address => {
						if (address.whetherDefault == "false") {
							address.whetherDefault = false;
						} else if (address.whetherDefault == "true") {
							address.whetherDefault = true;
						}
					})
				} else {
					this.$msg('查询地址信息失败{}', res.msg);
				}
			})
		},
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//this.$api.prePage()获取上一页实例，在App.vue定义
				var pages = getCurrentPages(),
					prevPage = pages[pages.length - 2];
				prevPage.addressData = item;
				prevPage.setData({
					addressData: item
				})
				uni.navigateBack()
			}
		},
		addAddress(type, item) {
			let itemTemp = new Object;
			if (item != undefined) {
				itemTemp = item;
			}
			itemTemp.type = type;
			uni.navigateTo({
				url: '/pages/address/addressManager?item=' + encodeURIComponent(JSON.stringify(itemTemp))
			});
		},
		//添加或修改成功之后回调
		refreshList(data, type) {
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.queryAddress();
		},

		touchS: function(e) {
			// console.log('touchS')
			if (e.touches.length == 1) {
				//设置触摸起始点水平方向位置
				this.startX = e.touches[0].clientX
				// console.log(this.startX)
			}
		},
		touchM: function(e) {
			// console.log('touchM')
			if (e.touches.length == 1) {
				//手指移动时水平方向位置
				var moveX = e.touches[0].clientX;
				//手指起始点位置与移动期间的差值
				var disX = this.startX - moveX;
				var delBtnWidth = this.delBtnWidth;
				var txtStyle = "";
				if (disX == 0 || disX < 0) { //如果移动距离小于等于0，说明向右滑动，文本层位置不变
					txtStyle = "left:0px";
				} else if (disX > 0) { //移动距离大于0，文本层left值等于手指移动距离
					txtStyle = "left:-" + disX + "px";
					if (disX >= delBtnWidth) {
						//控制手指移动距离最大值为删除按钮的宽度
						txtStyle = "left:-" + delBtnWidth + "px";
					}
				}
				//获取手指触摸的是哪一项
				var index = e.currentTarget.dataset.index;
				var list = this.addressList;
				list[index].txtStyle = txtStyle;
				// console.log("值",list[index].txtStyle)
				//更新列表的状态
				this.addressList = list;
			}
		},
		touchE: function(e) {
			// console.log('touchE')
			if (e.changedTouches.length == 1) {
				//手指移动结束后水平位置
				var endX = e.changedTouches[0].clientX;
				//触摸开始与结束，手指移动的距离
				var disX = this.startX - endX;
				var delBtnWidth = this.delBtnWidth;
				//如果距离小于删除按钮的1/2，不显示删除按钮
				var txtStyle = disX > delBtnWidth / 2 ? "left:-" + delBtnWidth + "px" : "left:0px";
				//获取手指触摸的是哪一项
				var index = e.currentTarget.dataset.index;
				var list = this.addressList;
				list[index].txtStyle = txtStyle;
				// console.log("LIST{}",list)
				//更新列表的状态{
				this.addressList = list;
			}
		},
		//点击删除按钮事件
		delItem: function(e) {
			//获取列表中要删除项的下标
			var addressId = e.currentTarget.dataset.addressId;
			this.deleteMember(addressId);
		},
		deleteMember(id){
			this.$minApi.uniapp_post(this.$base_api.deleteAddress, {
				addressId: id}).then(res => {
				if (res.success) {
					console.log("删除地址成功");
					this.queryAddress();
				} else {
					this.$msg('删除地址信息失败{}', res.msg);
				}
			})
		}
	}
}
